import React from "react"
import {
  chakra,
  Box,
  Button,
  Flex,
  useColorModeValue,
  Text,
  Drawer,
  DrawerBody,
  DrawerOverlay,
  DrawerContent,
  Spacer,
  useDisclosure,
} from "@chakra-ui/react"
import {
  FaBars,
  FaTimes,
} from "react-icons/fa"
import Link from "./overrides/Link"
import Logo from "./Logo"
import Container from "./Container"
import DesktopNav from "./nav/DesktopNav"
import "../styles/global.css"
import MobileNav from "./nav/MobileNav"
import SecondaryNav from './nav/SecondaryNav'

const DrawerNavMobile = () => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  const btnRef = React.useRef()

  return (
    <>
      <Button
        ref={btnRef}
        colorScheme="gray"
        variant="outline"
        border="0"
        _hover={{
          bgColor: '#ffffff00'  
        }}
        onClick={isOpen ? onClose : onOpen}
        display={["block", "block", "block", "none"]}
        aria-label="Open Nav"
      >
        {isOpen ? <FaTimes /> : <FaBars color="white"/>}
        <chakra.span sx={{ display: "none" }}>Open mobile nav</chakra.span>
      </Button>
      <Drawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
        finalFocusRef={btnRef}
      >
        <DrawerOverlay>
          <DrawerContent>

            <DrawerBody>
              <MobileNav menuName="primary" displayLocation="mobile" display="flex" />
            </DrawerBody>

          
          </DrawerContent>
        </DrawerOverlay>
      </Drawer>
    </>
  )
}

const Layout = (props) => {
  const bg = "white"
  const btnRef = React.useRef()
  return (
    <Box>
      <chakra.header
        top="0"
        position="absolute"
        zIndex="99"
        bg='#ffffff00'
        left="0"
        right="0"
        width="full"
        {...props}
      >
        <Container pt={0} px={0} mt="0" height="auto" mx="auto" minHeight="0">
          <Flex
            w="100%"
            h="100%"
            py={3}
            px={5}
            align="center"
            justify="space-between"
          >
            <Flex align="center">
              <Link to="/" aria-label="Home Page">
                <Logo />
                <chakra.span sx={{ display: "none" }}>Homepage</chakra.span>
              </Link>
              <DesktopNav display={{ base: "none", md: "flex" }} />
            </Flex>

            <Flex>
              <Spacer mx={1} />
              <SecondaryNav btnRef={btnRef} />
              <Spacer mx={1} />
              <DrawerNavMobile btnRef={btnRef} />
            </Flex>
          </Flex>
        </Container>
      </chakra.header>
      <Box as="main">
        {props.children}
      </Box>
      <chakra.footer
        bottom="0"
        zIndex="1"
        bg={bg}
        left="0"
        right="0"
        border="0"
        width="full"
        {...props}
      >
        <chakra.div border="0" height="6rem" mx="auto" maxW="1200px">
          <Flex
            w="100%"
            h="100%"
            py={3}
            px={5}
            align="center"
            justify="space-around"
            border="0"
          >
            <Text>
              Development sponsored by{" "}
              <a
                href={`https://gatsbyjs.com/`}
                target={`_blank`}
                rel="noreferrer"
              >
                <Text
                  as={`span`}
                  border="0"
                  color={useColorModeValue(`blue.600`, `#0cf`)}
                >
                  Gatsby
                </Text>
              </a>
            </Text>
          </Flex>
          <Flex
            border="0"
            w="100%"
            h="100%"
            py={3}
            px={5}
            align="center"
            justify="space-around"
          >
Something here too!
          </Flex>
        </chakra.div>
      </chakra.footer>
    </Box>
  )
}

export default Layout